/*Layout*/

@import "colors.scss";

$leftWidth: 25vw;
$rightWidth: 20vw;
$centerWidth: (100vw - ($leftWidth + $rightWidth));

$headerHeight: 48px;
$headerPadding: 5px;

* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

html {
	font-size: 100% !important;
	font-family: 'SourceSansPro', sans-serif !important;
}

body {
	min-width: 750px;      	/* 2x (LC fullwidth +
                          		CC padding) + RC fullwidth */
	position: fixed;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	margin: 0px;
	width: 100vw;
	height: 100vh;
	
    font-size: 100%;
	font-family: 'SourceSansPro', sans-serif;

  background-color: $colorBackground;
}		
		
#container {
	position: fixed;	
	padding-left: $leftWidth;   	/* LC fullwidth */
	padding-right: $rightWidth;  	/* RC fullwidth + CC padding */
    height: auto;
	bottom: 0px;
	top: $headerHeight;
	background: transparent;
}

#container .column {
	position: relative;
	float: left;
	height: 100%;
	background: inherit;
}

#center {
	padding: 0;    	/* CC padding */
	width: $centerWidth;
	background: $colorBackground;
	float:left;
}

#left {
	width: $leftWidth;          /* LC width */
	padding: 0;   /* LC padding */
	right: $leftWidth;          /* LC fullwidth + CC padding */
	margin-left: -100%;
	
	max-height: 100%;		
	background: $colorBackground;
	display: inline-block;
}
	
#right {
	width: $rightWidth;          /* RC width */
	padding: 0;    /* RC padding */
	margin-right: -($rightWidth);  /* RC fullwidth + CC padding */
	background: $colorBackground;
	float: left;
	margin-left: 0px;
	max-height: 100%;
	overflow-x: visible;
}

#header {
	/*DISPLAY*/
	float: left;
	width: 100%;
	height: $headerHeight;
	padding: $headerPadding;
			
	/*COLORS*/
	background: ($colorPrimary);
	color: ($colorPrimaryContrast);
	
	/*FONTS*/
	font-size: 1.5em;
	font-family: 'Montserrat', sans-serif;

	box-shadow: 0 0px 8px 0 rgba(0,0,0,0.12), 0 8px 8px 0 rgba(0,0,0,0.24);									
}

    #header-content-wrapper:before {
  	    content: '';
		display: inline-block;
		vertical-align: middle;
		height: 100%;
		margin-right: -0.25em;
    }

    #header-content-wrapper {
	    display: inline-block;
	    vertical-align: middle;
	    height: 100%;
	    width: 100%;
	    text-align: center;
    }

	#header-content {
			display: inline-block;
			vertical-align: middle;
		}
		
		/*** IE Fix ***/
		* html #left {
			left: 390px;           /* RC fullwidth */
		}

#menu .caret {
	visibility: hidden;
}

#menu .special-title {
	visibility: hidden;
}

#menu .btn.btn-default.dropdown-toggle:after {
	font-family: "Glyphicons Halflings";
	content:"\e236";
	margin-left: -.25em;

	position: absolute;
	left: 14px;
    font-size: 24px;
    top: 3px;
}

#menu .btn.btn-default.dropdown-toggle {
	position: absolute;
    left: 10px;
    z-index: 100;
    background-color: #009688;
    width: $headerHeight - ($headerPadding * 2);
	height: $headerHeight - ($headerPadding * 2);
	padding: 0;
    font-size: 1em;
    color: #fff;
    text-align: center;
    justify-content: center;
    vertical-align: middle;
    border: none;
    box-shadow: none;
}

	#menu .btn.btn-default.dropdown-toggle:hover {
		cursor: pointer;
		background-color: darken($colorPrimary, 5%);
	}

	

	#menu .btn.btn-default.dropdown-toggle:active,
	#menu .btn.btn-default.dropdown-toggle:focus {
		cursor: pointer;
		background-color: darken($colorPrimary, 10%);
	}

#menu .dropdown-menu.open {
	position: relative;
	top: 30px;
	left: 15%;
	height: auto;
	width: 120%;
	max-height: 25vh;
	transition: max-height .25s ease-in-out;
	background-color: #fff;
	border: none;
	border-radius: 2px;
	box-shadow: 0 0px 2px 0 rgba(0, 0, 0, 0.12), 0 2px 2px 0 rgba(0, 0, 0, 0.24);
	z-index: 101;
}

#menu .dropdown-menu>li>div {
	padding: 3px 20px;
	color: $colorGrey;
}

#header-content-wrapper .form-control {
    box-shadow: none;
    -webkit-box-shadow: none;
    width: auto;
    position: absolute;
	left: 0;
	background-color: transparent;
}

#righoverflow-menut .nya-bs-select.btn-group .dropdown-menu {
    width: 100% !important;
    min-width: initial !important;
}